import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { ClientUser, PageMetaData, SubscriptionInfo } from '@core/models';
import { DestroyService } from '@shared/services/destroy.service';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { PlaceAdService } from '@feature/place-ad/place-ad.service';
import { Observable, takeUntil } from 'rxjs';
import { ButtonType } from '@shared/components/button/button.component';
import { SellerType } from '@core/enums';
import { SubscriptionTier } from '@core/enums/profile.enum';
import { PlaceAdFacade } from '@feature/place-ad/place-ad.facade';
import { LIMITED_COUPON_CODE } from '@feature/place-ad/place-ad.constant';
import { MatDialog } from '@angular/material/dialog';
import { LimitReachedDialogComponent } from '@feature/agent-hub/pages/listings/limit-reached-dialog/limit-reached-dialog.component';
import { SubscriptionOfferDialogComponent } from '@feature/my-profile/v2/components/subscription-offer-dialog/subscription-offer-dialog.component';

@Component({
    selector: 'findqo-place-ad-select-account',
    templateUrl: './place-ad-select-account.component.html',
    styleUrls: ['./place-ad-select-account.component.scss'],
    providers: [DestroyService],
    standalone: false
})
export class PlaceAdSelectAccountComponent implements OnInit {
	constructor(
		private route: ActivatedRoute,
		private router: Router,
		private placeAdService: PlaceAdService,
		private destroy: DestroyService,
		private myProfileFacade: MyProfileFacade,
		private placeAdFacade: PlaceAdFacade,
		private dialog: MatDialog
	) {
		this.user$ = myProfileFacade.getUser$();
		this.subscription$ = this.myProfileFacade.getSubscriptionInfo$();
	}

	user$: Observable<ClientUser>;
	hasBusinessProfile = false;
	readonly buttonType = ButtonType;
	@Input() hasAgents: boolean;
	userInfo: ClientUser;
	hasPlanAdsLeft: boolean;

	subscription: SubscriptionInfo;
	subscription$: Observable<SubscriptionInfo>;

	//check if user has businsess profile or not based on selection and navigate accordingly
	gotoSelection(isBusinessAccount = false): void {
		if (isBusinessAccount) {
			this.subscription$.pipe(takeUntil(this.destroy)).subscribe((subscription) => {
				this.subscription = subscription;
			});

			if (this.subscription.status === 'cancelled' && new Date(this.subscription.dates.expired) < new Date()) {
				this.dialog.open(SubscriptionOfferDialogComponent, {
					disableClose: true,
					width: '500px',
					height: '600px',
				});

				return;
			}
		}

		//if individual account or has business account without agents
		if (!isBusinessAccount || this.hasBusinessProfile) {
			if (isBusinessAccount && this.userInfo.businessProfile?.subscription.reference === SubscriptionTier.LIMITED_YEARLY) {
				this.placeAdFacade
					.getPaymentCount({
						userId: this.userInfo?.id,
						sellerTypeId: '2',
						couponCode: LIMITED_COUPON_CODE,
					})
					.pipe()
					.subscribe((meta: PageMetaData) => {
						this.hasPlanAdsLeft = +(meta?.total ?? 0) < 2;

						if (this.hasPlanAdsLeft) {
							this.router.navigate([APP_ROUTE.placeAdCategory], {
								queryParams: { sellerTypeId: 2 },
							});
						} else {
							this.dialog.open(LimitReachedDialogComponent, {
								disableClose: true,
								maxWidth: '500px',
								maxHeight: '354px',
							});
						}
					});

				return;
			}
			this.router.navigate([APP_ROUTE.placeAdCategory], {
				queryParams: { sellerTypeId: isBusinessAccount ? SellerType.BUSINESS : SellerType.INDIVIDUAL },
			});
		} else {
			//if selected business account and has agents
			if (!(this.hasBusinessProfile && this.hasAgents)) {
				//if no business account and selected business account
				this.router.navigate([APP_ROUTE.placeAdBusiness]);
			}
		}
	}

	getUserDetails(): void {
		this.user$.pipe(takeUntil(this.destroy)).subscribe((user: ClientUser) => {
			this.userInfo = user;
			this.hasBusinessProfile = !!user?.businessProfile;
		});
	}

	ngOnInit(): void {
		this.getUserDetails();
		this.placeAdService.setCurrentSellerId(undefined);
	}
}
